<template>
  <el-container>
    <el-main>
      <div class="head-title" style="    float: right;margin-right: 135px;margin-bottom: 20px;">
        <!---->
        <el-button type="primary" size="small" icon="el-icon-circle-plus"  @click="dialogFormVisible = true"></el-button>
      </div>
      <div class="table-box">
        <el-table :data="userData">
          <el-table-column
            prop="username"
            label=""
            align="center"
            width="200"
          >

          </el-table-column>
          <el-table-column
            prop="email"
            label=""
            align="center"
            width="300"
          >
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            width="200"
            prop="user_phone"
          >
          </el-table-column>
          <el-table-column
            prop="user_type"
            label=""
            align="center"
            width="200"
          >
            <template slot-scope="scope">
              {{scope.row.user_type|filterType}}
            </template>
          </el-table-column>
          <el-table-column
            prop="opera"
            label=""
            align="center"
            width="300"
          >
            <template slot-scope="scope">
              <el-button type="primary" size="small" icon="el-icon-edit" @click="openupdate(scope.row.id)"><span
                style="padding: 15px auto"></span></el-button>
              <el-button type="primary" icon="el-icon-remove" size="small" @click="opendelete(scope.row.id)">
                <span></span></el-button>
              <!--<el-button class="btn-primary" icon="el-icon-plus"><span style="padding: 15px auto"></span></el-button>-->
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!------------------------------------------>
      <el-dialog title="" :visible.sync="dialogFormVisible" >
        <el-form :model="userform" style="width: 500px">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="userform.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="userform.password" autocomplete="off" type="password"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="userform.email" autocomplete="off" ></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="userform.phone" autocomplete="off" ></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="userform.user_type" placeholder="" style="width: 380px;">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
      </el-form-item>
        </el-form>
         <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false"> </el-button>
    <el-button type="primary" @click="addUser()"> </el-button>
  </div>
      </el-dialog>
      <!------------------------------------------>
      <el-dialog title="" :visible.sync="dialogUpdateFormVisible" >
        <el-form :model="updateform" style="width: 500px">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="updateform.username" autocomplete="off" disabled></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="updateform.password" autocomplete="off" type="password"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="updateform.email" autocomplete="off" ></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="updateform.phone" autocomplete="off" ></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="userform.user_type" placeholder="" style="width: 380px;">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
      </el-form-item>
        </el-form>
         <div slot="footer" class="dialog-footer">
    <el-button @click="dialogUpdateFormVisible = false"> </el-button>
    <el-button type="primary" @click="updateUser()"> </el-button>
  </div>
      </el-dialog>
    </el-main>
  </el-container>
</template>

<script>
  export default {
    name: "userManage",
    data() {
      return {
        userData: [],
        dialogFormVisible: false,
        dialogUpdateFormVisible:false,
        userform:{
          username:"",
          user_type:"",
          password:"",
          email:"",
          phone:"",
        },
        updateform:{
          username:"",
          user_type:"",
          password:"",
          email:"",
          phone:"",
        },
        formLabelWidth: '120px'
      }
    },
    mounted() {
    },
    created() {
      this.GetUser();
    },
    methods: {
      // 

      openupdate(uid){
         let data = {"user_id":uid};
         this.$http({
          method: "POST",
          url: this.$store.state.location + "/api/user/get_user_infor/",
          data: this.$Qs.stringify(data)
        }).then(response => {
          let data = response.data;
          this.updateform.username = data[0].username;
          // this.updateform.password = data[0].password;
          this.updateform.email = data[0].email;
          this.updateform.phone = data[0].user_phone;
          this.updateform.user_type = data[0].user_type;
          this.dialogUpdateFormVisible = true;
          console.log(data);
        }).catch(error => {
          console.log(error)
        })
      },
      updateUser(){
        this.dialogUpdateFormVisible = false;
        let data = {
          "username":this.updateform.username,
          "user_type":this.updateform.user_type,
          "email":this.updateform.email,
          "phone":this.updateform.phone,
          "password":this.updateform.password,
          "csrfmiddlewaretoken":this.getCookie('csrftoken')
        };
        this.$http({
          url:this.$store.state.location+"/api/user/update_user/",
          method:"POST",
          data:this.$Qs.stringify(data)
        }).then(response=>{
           let data = response.data;
           if(data === "ok"){
             this.$message({
               type:"info",
               message:""
             });
             this.GetUser();
           }else{
             this.$message({
               type:"error",
               message:""
             })
           }
        }).catch(error=>{
          console.log(error)
        })
      },
      // 
      addUser(){
        this.dialogFormVisible = false;
        let data = {
          "username":this.userform.username,
          "user_type":this.userform.user_type,
          "email":this.userform.email,
          "phone":this.userform.phone,
          "password":this.userform.password,
          "csrfmiddlewaretoken":this.getCookie('csrftoken')
        };
        if(this.userform.user_type===""||this.userform.username === "" || this.userform.password===""||this.userform.email === "" || this.userform.phone===""){
          this.$message({
            type: 'info',
            message: ''
          });
        }
        else{
          this.$http({
          url:this.$store.state.location+"/api/user/add_user/",
          method:"POST",
          data:this.$Qs.stringify(data)
        }).then(response=>{
          let data = response.data;
          if(data === "ok"){
            this.$message({
              type:"success",
              message:""
            });
            this.GetUser();
          }else {
            this.$message({
              type: "error",
              message: ","
            })
          }
        }).catch(error=>{
          console.log(error)
        })
        }

      },
      // 
      opendelete(uid) {
         this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteUser(uid);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      // 
      deleteUser(uid){
        this.$http({
          url:this.$store.state.location +"/api/user/delete_user/",
          method:"POST",
          data:this.$Qs.stringify({"user_id":uid})
        }).then(response =>{
          let data = response.data;
          if(data === "ok"){
            this.$message({
              type:"success",
              message:""
            });
            this.GetUser();
          }else{
            this.$message({
              type:"error",
              message:""
            })
          }
        })
      },
      // 
      GetUser() {
        this.$http({
          method: "POST",
          url: this.$store.state.location + "/api/user/get_user_infor/",
          data: {}
        }).then(response => {
          let data = response.data;
          this.userData = data;
          console.log(data);
        }).catch(error => {
          console.log(error)
        })
      }
    }

  }
</script>

<style scoped>
  .table-box {
    width: 1200px;
    margin: 20px auto;
  }
</style>



